@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu';
}

#root {
  display: flex;
  justify-content: center;
  -webkit-transform: translateY(60%);
          transform: translateY(60%);
}

@media screen and (max-width: 500px) {
  #root {
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
  }
}
.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 600px;
  background-color: rgba(0,0,0,.5);
  border-radius: 10%;
  color: rgb(223, 223, 223);
  box-shadow: 0 0 60px 5px black, inset 0 0 30px 3px black;
}

.App h1 {
  flex: 0 0 100%;
  text-align: center;
  font-size: 4em;
  margin-top: 0;
}

body {
  background-image: url(/pomodoro-clock/static/media/calm.c0682b14.jpg);
  background-size: 2600px 1733px;
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 500px) {
  .App {
    border-radius: 0;
  }
}
.time-settings {
  margin-top: -50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex: 0 0 45%;
}

.time-settings h2 {
  width: 100%;
  height: 5px;
}

.time-settings #break-length {
  height: 30px;
  width: 30px;
  font-size: 25px;
}

.time-settings #session-length {
  height: 30px;
  width: 30px;
  font-size: 25px;
}

.time-settings button {
  background: none;
  border: none;
  height: 25px;
  font-size: 30px;
  color: rgb(223, 223, 223);
}

.time-settings button:active {
  color: grey;
}

.time-settings button:focus {
  outline: none;
}

.time-settings #session-label, #break-label {
  font-size: 30px;
}

@media screen and (max-width: 500px) {
  .time-settings #session-label, #break-label {
    padding-bottom: 15px;
  }
}
@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff") format("woff"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7") format("svg");
}

.display {
  flex: 0 0 350px;
  text-align: center;
  height: 180px;
  box-shadow: 0 0 10px 0 black, inset 0 0 20px 2px black;
  border-radius: 50px;
  background-color: rgba(0,0,0,.3); 
}

.display #timer-label {
  font-weight: bold;
  font-size: 50px;
  margin-top: 10px;
}

.display #time-left {
  position: relative;
  font-size: 80px;
  height: 0;
  top: -80px;
  font-family: 'Digital';
}
.controller {
  flex: 0 0 100%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 10px;
}

.controller i {
  font-size: 40px;
}

.controller #reset {
  margin-left: 30px;
}

.controller span:hover {
  cursor: pointer;
}
